import React from 'react'

function Service() {
    return (
        <div class="services p-3" id="services">
            <h1 className="text-ubuntu">Services</h1>
            <div class="container">
                <h1>A Vision For Business Growth</h1>
                <p>The company has closely monitored the sustained growth of the
                    overall economic situation during the last 10 years, emphasising
                    the importance of shaping corporate growth objectives in step with
                    these general economic expansion trend. The company aims
                    to increase more market share for domestic and overseas countries
                    by expand domestic and overseas branches office, with the
                    ultimate goal of becoming a leader in target businesses.</p>

                <ul>
                    <li>Hotel Management System</li>
                    <li>Point of sale System</li>
                    <li>Accounting System</li>
                    <li>Hr system</li>
                </ul>
            </div>

        </div>
    )
}

export default Service
