import React from "react";
import Logo from "./images/zonasys_icon.png";

function Title() {
  return (
    <div classNameName="container-fluid">
      <div
        id="carouselExampleControls"
        className="carousel slide"
        data-ride="carousel"
      >
        <div className="carousel-inner bg-success">
          <div className="carousel-item active" id="carousel-item1">
            
            <div className="container text-center text-light">
            <img src={Logo} alt="" />
              <h1 className="text-ubuntu">Unveil its "Goal"</h1>A
              Vision For Business Growth
              <p>
                The company aims to increase more market share for domestic and
                overseas countries by expand domestic and overseas branches
                office, with the ultimate goal of becoming a leader in target
                businesses.
              </p>
              <button className="btn btn-outline-light">Get Started</button>
              <hr />
            </div>
          </div>
          <div className="carousel-item" id="carousel-item2">
            {/* <img src="https://images.pexels.com/photos/1148820/pexels-photo-1148820.jpeg?cs=srgb&dl=pexels-panumas-nikhomkhai-1148820.jpg&fm=jpg" className="d-block w-100" alt="..." /> */}
          </div>
          <div className="carousel-item" id="carousel-item3">
            <img src="..." className="d-block w-100 h-450" alt="..." />
          </div>
        </div>
        <a
          className="carousel-control-prev"
          href="#carouselExampleControls"
          role="button"
          data-slide="prev"
        >
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="sr-only">Previous</span>
        </a>
        <a
          className="carousel-control-next"
          href="#carouselExampleControls"
          role="button"
          data-slide="next"
        >
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="sr-only">Next</span>
        </a>
      </div>
    </div>
  );
}

export default Title;
